/* Style inputs with type="text", select elements and textareas */
input[type=text],
select,
textarea {
    width: 100%;
    /* Full width */
    padding: 12px;
    /* Some padding */
    border: 1px solid #ccc;
    /* Gray border */
    border-radius: 4px;
    /* Rounded borders */
    box-sizing: border-box;
    /* Make sure that padding and width stays in place */
    margin-top: 6px;
    /* Add a top margin */
    margin-bottom: 16px;
    /* Bottom margin */
    /* Allow the user to vertically resize the textarea (not horizontally) */
    background: linear-gradient(0deg, #00000088 100%, #ffffff44 100%);
    color: white;
}

.submit-button-container {
    text-align: right;
} 

input:focus, textarea:focus {
    border-color: #bb86fc;
    outline: none;
}

#email:focus {
    border-color: #bb86fc;
    outline: none;
}
input[type=email],
select,
textarea {
    width: 100%;
    /* Full width */
    padding: 12px;
    /* Some padding */
    border: 1px solid #ccc;
    /* Gray border */
    border-radius: 4px;
    /* Rounded borders */
    box-sizing: border-box;
    /* Make sure that padding and width stays in place */
    margin-top: 6px;
    /* Add a top margin */
    margin-bottom: 16px;
    /* Bottom margin */
    /* Allow the user to vertically resize the textarea (not horizontally) */
    background: linear-gradient(0deg, #00000088 100%, #ffffff44 100%);
    color: white;
}

input[type=message],
textarea {
    resize: vertical;
    /*POSITION:;*/
    width: 100%;
    /* Full width */
    padding: 12px;
    /* Some padding */
    border: 1px solid #ccc;
    /* Gray border */
    border-radius: 4px;
    /* Rounded borders */
    box-sizing: border-box;
    /* Make sure that padding and width stays in place */
    margin-top: 6px;
    /* Add a top margin */
    margin-bottom: 16px;
    /* Bottom margin */

    bottom: 20px;
    /* Allow the user to vertically resize the textarea (not horizontally) */
    background: linear-gradient(0deg, #00000088 100%, #ffffff44 100%);
    color: white;

    height: 200px;
}

::placeholder {
    /* Chrome, Firefox, Opera, Safari 10.1+ */
    color: white;
    opacity: 1;
    /* Firefox */
}

:-ms-input-placeholder {
    /* Internet Explorer 10-11 */
    color: white;
}

::-ms-input-placeholder {
    /* Microsoft Edge */
    color: white;
}

