legend {
    color:#bb86fc;
    font-weight: bolder;
    font-style: italic;
    padding-left: 4px;
    padding-right: 4px;
}

.about-below {
    padding: 5px 30px 5px 30px;
    width: 368px;
    display: flex;
    justify-content: space-between;
}

#box {
    display: flex;
    justify-content: center;
    align-items: center;
    border: 3px solid #ff0000;
    box-sizing: border-box;
    animation: border-animation 2.5s infinite linear;
    border-radius: var(--primer-borderRadius-large, 8px) !important;
}


@keyframes border-animation {
0% {
    border-color: #1d1d1d;
}
25% {
    border-color: #BF40BF;
}
50% {
    border-color: #1d1d1d;
}
75% {
    border-color: #BF40BF;
}
100% {
    border-color: #1d1d1d;
}
}


.card-bottom:hover {
    animation: slidebg 3s linear infinite;
    background-image: linear-gradient(90deg, #2d2d2d 49%, #bb86fc 100%);
}

.card-bottom-inside:hover {
    border: 0px;
    margin: 2px;

}


.buttontext:hover {
    text-align: center;

    background-clip: text;
    -webkit-background-clip: text;
    color: transparent;
    animation: slidebg 2s linear infinite;
    background-image: linear-gradient(90deg, #bb86fc 0%, white 49%, white 80%, #bb86fc 100%);
    font-weight: bolder;
}
}

