/* For desktops only */
/* @media (min-width: 1024px) { } */

.Navigation-button {
            width: 275px;
            height: 50px;
            margin-top: 15px;

    border-radius: var(--primer-borderRadius-large, 8px) !important;
    position: relative !important;
    /* width: 100%; */
    background-COLOR: BLACK;
    color: white;
    box-shadow: 0px 0px 60px 5px #000000;
    transition: all 1s ease;
    max-height: 56px;
    display: flex;

}

.card-bottom-inside {
    background-color: #1d1d1d;
    border-radius: 6px;
    width: 100%;
    BORDER: solid #2d2d2d;

}

.button-text-container {
    text-decoration: none;

    text-align: center; color: white;
}

@keyframes border-animation {
0% {
    border-color: #1d1d1d;
}
25% {
    border-color: #BF40BF;
}
50% {
    border-color: #1d1d1d;
}
75% {
    border-color: #BF40BF;
}
100% {
    border-color: #1d1d1d;
}
}

@keyframes slidebg {
    to {
        background-position: 30vw;
    }
}
.Navigation-button:hover {
    animation: slidebg 1s linear infinite;
    background-image: linear-gradient(90deg, #2d2d2d 49%, #bb86fc 100%);
}

.card-bottom-inside:hover {
    border: 0px;
    margin: 2px;

}

.buttontext:hover {
    text-align: center;

    background-clip: text;
    -webkit-background-clip: text;
    color: transparent;
    animation: slidebg 1s linear infinite;
    background-image: linear-gradient(90deg, #bb86fc 0%, white 49%, white 80%, #bb86fc 100%);
    font-weight: bolder;
}

.buttontext:hover {
    text-align: center;

    background-clip: text;
    -webkit-background-clip: text;
    color: transparent;
    animation: slidebg 2s linear infinite;
    background-image: linear-gradient(90deg, #bb86fc 0%, white 49%, white 80%, #bb86fc 100%);
    font-weight: bolder;
}